import {Table} from '@/modules/eos/table';
import {EOS} from '@/modules/eos/eos';
import {Asset} from '@/modules/eos/asset';
import i18n from '@/lang';
import {BaseBlackjackGame, BlackjackResult} from '@/modules/contract/blackjack/blackjackGame';
import {LocaleMessage} from 'vue-i18n';
import {Token} from '@/modules/eos/token';

export class BlackjackHistoryRow extends BaseBlackjackGame {
    public readonly payout: Asset[];

    constructor(value: any) {
        super(value);
        this.payout = value.payout.map((asset: string) => Asset.fromString(asset));
    }

    public getResultString(): LocaleMessage {
        switch (this.result) {
            case BlackjackResult.Surrender:
                return i18n.t('bj_result_surrender');
            case BlackjackResult.Lose: {
                return this.playerPoints > BaseBlackjackGame.MAX_POINTS ?
                        i18n.t('bj_result_player_busted') : i18n.t('bj_result_banker_win');
            }
            case BlackjackResult.Push:
                return i18n.t('bj_result_push');
            case BlackjackResult.Win:
                if (this.isPlayerBlackjack()) {
                    return i18n.t('bj_result_player_blackjack');
                }
                return this.bankerPoints > BaseBlackjackGame.MAX_POINTS ?
                        i18n.t('bj_result_banker_busted') : i18n.t('bj_result_plaer_win');
            default:
                throw new Error('Invalid Game Result');
        }
    }

    public toString(): string {
        return this.getResultString().toString() + ` (${this.playerPoints}:${this.bankerPoints})`;
    }

    public hasEOSPayout(): boolean {
        return Asset.findBySymbol(this.payout, Token.EOS.symbol).quantity > 0;
    }

    public EOSPayoutString(): string {
        return Asset.findBySymbol(this.payout, Token.EOS.symbol).toString();
    }
}

export class BlackjackHistoryTable extends Table<BlackjackHistoryRow> {
    constructor(eos: EOS, contract: string) {
        super(eos, 'history', 'id', contract);
    }

    protected convert(row: any[]): BlackjackHistoryRow[] {
        return row.map((value) => new BlackjackHistoryRow(value)).sort((a, b) =>
                b.closeTime - a.closeTime,
        );
    }
}
